export default {
	data() {
		return {
			soure:true,
			cancle:false
		}
	},
	methods: {
		sourelse:function(){
			let _this = this
			if(_this.soure == true){
				uni.showModal({
					title: '是否取消关注',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
							_this.soure = !_this.soure;
							_this.cancle = !_this.cancle;
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				});
			}else{
				_this.soure = !_this.soure;
				_this.cancle = !_this.cancle;
			}
			
		}
	}
}
